.program {
  padding-top: 64px;
  padding-bottom: 100px;

  @media (max-width: 1299px) {
    padding: 50px 0;
  }

  @media (max-width: $mobiles-max) {
    padding: 35px 0;
  }
}

.program__inner {
  padding: 0 5px;

  .swiper-slide {
    height: auto !important;
  }

  .swiper-wrapper {
    padding: 0 0 50px;
  }

  .swiper-pagination-bullet {
    width: 16px;
    height: 16px;

    background: rgba(93, 6, 235, 0.5);
  }

  .swiper-pagination-bullet-active {
    width: 17px;
    height: 16px;

    background: #5d06eb;
  }

  .swiper-pagination {
    @media (min-width: $tablets) {
      display: none;
    }
    @media (max-width: $mobiles-max) {
      bottom: -4px !important;
    }
  }
}

.program__title {
  display: block;
  text-align: center;
  margin-bottom: 23px;

  @media (max-width: $mobiles-max) {
    margin-bottom: 36px;
  }

  h2 {
    font-style: normal;
    padding: 0 18px;
    font-weight: bold;
    font-size: 44px;
    line-height: 80px;
    background-color: #fde509;

    letter-spacing: 0.025em;

    color: $color-default-black;
    text-align: center;
    text-transform: uppercase;
    display: inline;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      padding: 0 6px;
      line-height: 41px;

      text-align: center;
      letter-spacing: 0.025em;
    }
  }
}

.program__swiper {
  display: flex;
}

.program__item {
  background: $color-default-white;
  border: 2px solid #fd6108;
  border-radius: 25px;
  min-height: 562px;

  padding: 31px 50px 31px 34px;

  @media (max-width: $mobiles-max) {
    min-height: 607px;
  }
}

.program__date {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;

  color: #fd6a0a;

  margin-bottom: 15px;
  display: block;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    margin-bottom: 30px;
  }
}

.program__day {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 32px;
  font-family: 'Bebas Neue', sans-serif;
  margin-bottom: 0;

  letter-spacing: 0.025em;

  color: #000000;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;

    letter-spacing: 0.025em;
  }
}

.program__info-item {
  margin-bottom: 15px;

  @media (max-width: $mobiles-max) {
    margin-bottom: 23px;
  }

  p {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 22px;
    color: #353535;
    margin-bottom: 0;

    @media (max-width: $mobiles-max) {
      margin-bottom: 2px;
    }
  }

  span {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;

    color: #323232;

    letter-spacing: 0.2px;
  }
}
