.form {
  background-image: url('../img/bg-footer.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 523px;

  padding: 132px 0;

  @media (max-width: $tablets-max) {
    padding: 50px 0;
    min-height: 450px;
    background-position: 25.5% 28px;
  }

  @media (max-width: $mobiles-max) {
  }
}

.form__agree {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  white-space: nowrap;

  color: #545454;

  @media (max-width: $mobiles-max) {
    white-space: pre-wrap;
  }
}

// input, textarea {
//   outline:none;
// }

// //      focus
// input:focus, textarea:focus {
//   outline:none;
// }

.form__body {
  display: flex;

  @media (max-width: $tablets-max) {
    flex-direction: column;
  }

  @media (max-width: $mobiles-max) {
    padding: 0 5px;
  }
}

.form__send {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 20px;

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin-top: 14px;
  }
}

.form__texts {
  width: 40%;
  margin-right: 3.5%;

  padding-top: 38px;

  @media (max-width: $tablets-max) {
    width: 100%;
    margin-right: 0;
  }

  @media (max-width: $mobiles-max) {
    padding-top: 55px;
  }
}

.form__texts-title {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 46px;
  background-color: #fde509;

  padding: 5px 10px 0;

  color: #000000;

  display: inline;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 115%;

    padding: 5px 5px 0;
  }
}

.form__texts-subtitle {
  font-family: 'Bebas Neue', sans-serif;
  background-color: #ebdfff;
  display: block;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 37px;

  margin-bottom: 24px;

  padding-right: 30px;

  color: #000000;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 115%;

    padding: 5px 5px;

    margin-bottom: 10px;
  }
}

.form__texts-detail {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  @media (max-width: $mobiles-max) {
    margin-bottom: 16px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;
    margin: 0;
    letter-spacing: 0.4px;

    color: #000000;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 18px;
    }
  }

  svg {
    flex-shrink: 0;

    margin-right: 10px;
  }
}

.form__texts-marathon {
  margin-top: 63px;

  @media (max-width: $mobiles-max) {
    margin-top: 30px;
  }
}

.form__texts-marathon-title {
  font-family: 'Bebas Neue', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  margin-bottom: 8px;

  text-align: center;
  letter-spacing: 0.05em;

  color: #545454;
}

.form__texts-timer {
  display: flex;
  height: 103px;

  @media (max-width: $tablets-max) {
    justify-content: center;
  }
  p {
    font-style: normal;
    font-weight: bold;
    font-size: 56px;
    line-height: 64px;
    text-align: center;
    letter-spacing: 0.455em;
    text-transform: uppercase;
    background-image: url('../img/group.svg');
    background-repeat: no-repeat;
    color: #ffffff;
    height: 103px;
    width: 111px;
    margin: 0;
    padding-left: 10px;

    @media (max-width: $mobiles-max) {
      background-size: contain;
      width: 106px;
    }

    @media (max-width: 470px) {
      width: 78px;
      font-size: 40px;
      line-height: 54px;
      padding-left: 8px;
    }
  }

  span {
    font-style: normal;
    font-weight: bold;
    font-size: 56px;
    line-height: 64px;
    text-align: center;
    letter-spacing: 0.455em;
    text-transform: uppercase;
    color: #701df7;
    width: 20px;
    transform: translateX(-2px);
  }

  b {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transform: translateY(-4px);

    color: #2b2b2b;
  }
}

.form__time {
  display: flex;
  margin-right: 13px;
  flex-direction: column;

  @media (max-width: $mobiles-max) {
    margin-right: 11px;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.form__request {
  background: #fe810f;
  border-radius: 25px;
  padding: 127px 158px 100px;
  width: 58.5%;
  min-height: 523px;
  transition: $transition-settings;

  @media (max-width: 1299px) {
    padding: 127px 100px;
  }

  @media (max-width: $tablets-max) {
    width: 100%;
    margin-right: 0;
    padding: 45px 45px;
    margin-top: 27px;
    min-height: 348px;
  }

  @media (max-width: 470px) {
    padding: 45px 15px;
  }
}

[placeholder] {
  transition: all $transition-settings;
}

:-webkit-input-placeholder {
  -webkit-transition: all $transition-settings;
  transition: all $transition-settings;
}

:-moz-placeholder {
  -moz-transition: all $transition-settings;
  transition: all $transition-settings;
}

::-moz-placeholder {
  -moz-transition: all $transition-settings;
  transition: all $transition-settings;
}

:-ms-input-placeholder {
  -ms-transition: all $transition-settings;
  transition: all $transition-settings;
}

:focus[placeholder] {
  outline: none;
  transition: all $transition-settings;
}

:focus::-webkit-input-placeholder {
  outline: none;
  -webkit-transition: all $transition-settings;
  transition: all $transition-settings;
}

:focus::-moz-placeholder {
  outline: none;
  -moz-transition: all $transition-settings;
  transition: all $transition-settings;
}

:focus:-moz-placeholder {
  outline: none;
  -moz-transition: all $transition-settings;
  transition: all $transition-settings;
}

:focus:-ms-input-placeholder {
  outline: none;
  -ms-transition: all $transition-settings;
  transition: all $transition-settings;
}

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 30px;

  @media (max-width: $mobiles-max) {
    row-gap: 20px;
  }

  &:focus {
    outline: none !important;
  }
}

.form__field {
  position: relative;
  margin-bottom: 12px;

  @media (max-width: $mobiles-max) {
    margin-bottom: 10px;
  }
}
.form__input {
  background: rgba(255, 255, 255, 0.87);
  border-radius: 8px;
  width: 100%;
  height: 70px;
  padding: 0 15px;
  border: 1px solid transparent;
  transition: all $transition-settings;

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;

  color: #827e7e;

  @media (max-width: $mobiles-max) {
    width: 100%;
    height: 63px;
  }

  &:hover {
    // border: 1px solid $border-black;
  }

  &:focus {
    // border: 1px solid $border-black;
    opacity: 1 !important;
    padding-top: 15px;
  }

  &.error {
    // border: 1px solid $border-red;
  }

  &:valid,
  &:invalid {
    padding-top: 15px;
  }

  &--radio {
    width: 0 !important;
    height: 0;
    position: absolute;
    opacity: 0 !important;
    z-index: -1;
  }
}

.form__radio {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #fff;
  // border: 1px solid $border-black;
  border-radius: 50%;
  vertical-align: text-top;
  margin-right: 10px;
}

.form__radio::before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  // background: $bg-black;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: all $transition-settings;
}

.form__input--radio:checked + .form__radio::before {
  transform: translate(-50%, -50%) scale(1);
}

.form__message {
  display: flex;
  width: 100%;
  flex-direction: column;

  &-textarea {
    width: 100%;
    height: 170px;
    padding: 15px;
    border: 1px solid transparent;
    opacity: 0.5 !important;
    resize: none;
    transition: all $transition-settings;

    &:hover {
      // border: 1px solid $border-black;
    }

    &:focus {
      // border: 1px solid $border-black;
      opacity: 1 !important;
      padding-top: 25px;
    }

    &:valid,
    &:invalid {
      padding-top: 25px;
    }
  }
}

.form__label {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;

  color: #827e7e;
  position: absolute;
  top: 24px;
  left: 26px;

  transition: all $transition-settings;

  @media (max-width: $mobiles-max) {
    top: 21px;
    left: 24px;
  }
}

.form__field input:focus + label,
.form__field textarea:focus + label,
.form__field input:not(:placeholder-shown) + label,
.form__field textarea:not(:placeholder-shown) + label {
  top: 8px;
  left: 15px;
  font-size: 12px;
}

.form__errors {
  width: 100%;
  position: absolute;
  left: 0;
}

.form__checkbox-error {
  position: absolute;
  color: $color-red;
  top: 20px;

  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  margin: 0;
  white-space: nowrap;

  @media (max-width: $mobiles-max) {
    width: 211px;
    top: 32px;
    white-space: pre-wrap;
  }
}

.form__submit {
  max-width: 190px;
}

.form__accept {
  font-size: 12px;
  line-height: 120%;
  opacity: 0.5;

  &-link {
    text-decoration: underline;
  }
}

.form__length {
  position: absolute;
  left: 15px;
  bottom: 10px;
  font-size: 12px;
  line-height: 100%;
  opacity: 0.5;
}

.form__agree-wrapper {
  display: flex;
  align-items: center;
  position: relative;
  transform: translateX(16px);

  margin: 22px 0 0 0;

  @media (max-width: $mobiles-max) {
    margin: 16px 0 0 0;
    padding-left: 30px;
  }
}

.form__checkbox-input {
  position: relative;
}
.form__checkbox-input + label {
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    background: #f7f8fa;
    border: 1px solid #dfe6ed;
    border-radius: 2px;

    width: 24px;
    height: 24px;

    left: -33px;
    top: -6px;

    @media (max-width: $mobiles-max) {
      left: -7px;
      top: 0px;
    }
  }
}

.form__checkbox-input + label.checked {
  &::before {
    background: $color-default-white;
    border: none;
  }
}

.form__checkbox-input + label.checked::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  background-image: url('../img/ok.svg');
  background-repeat: no-repeat;
  z-index: 1;
  left: -30px;
  top: 0;

  @media (max-width: $mobiles-max) {
    left: -3px;
    top: 5px;
  }
}
