.message {
  display: none;

  &--shown {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    background-color: $color-red;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    z-index: 1000;
  }
}

.message__text {
  font-size: 35px;
  font-style: normal;
  font-weight: 700;
  line-height: 41px;
  letter-spacing: 0em;
  color: $color-white;
  text-align: center;
  margin: 0 auto;

  @media (max-width: $mobiles-max) {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
  }
}