.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: $transition-settings;
  cursor: pointer;
  flex-shrink: 0;

  &--consultation {
    background: linear-gradient(90deg, #5800e8 0%, #7d2dff 98.44%);
    border-radius: 60px;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;

    color: #ffffff;
    padding: 24px 0;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      line-height: 23px;
      text-align: center;

      padding: 22px 0;

      color: #ffffff;
    }
  }

  &--join {
    font-family: 'Ubuntu', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: $color-default-white;

    padding: 28px 0;

    width: 92%;
    min-width: 300px;

    background: linear-gradient(90deg, #5800e8 0%, #7d2dff 98.44%);
    box-shadow: 0px 0px 26px 3px rgba(53, 53, 53, 0.38);
    border-radius: 48px;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: bold;
      font-size: 28px;
      line-height: 32px;

      padding: 22px 0;
      width: 100%;

      text-align: center;

      color: #ffffff;
      min-width: 275px;
    }
  }

  .icon {
    stroke: $color-white;
    transition: $transition-settings;
  }

  &:hover {
    opacity: 0.8;

    p {
      color: $color-black;
    }
  }
}
