.banners {
  position: relative;
  background-image: url('../img/background.jpg');
  min-height: 1189px;
  padding-top: 128px;
  background-position: center -10px;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: $mobiles-max) {
    padding-top: 100px;
    min-height: 1164px;
  }

  @media (max-width: 500px) {
    background-image: url('../img/background-mob.jpg');
  }
}

.banners__inner {
  display: flex;
  position: relative;
}

.banners__subtitle {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  color: $color-default-black;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;

    color: #000000;

    width: 190px;
    margin-top: 22px;

    margin-bottom: 13px;
  }
}

.banners__page-title {
  margin-top: 33px;
  padding: 0 10px;

  margin-bottom: 14px;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: bold;
    font-size: 44px;
    line-height: 51px;
    max-width: 414px;

    margin-top: 22px;
    padding: 0;

    letter-spacing: 0.025em;

    color: #000000;
  }

  @media (max-width: 470px) {
    font-size: 30px;
  }

  span {
    display: block;
    background-color: $color-yellow;

    padding: 0 10px;
    margin-left: -10px;
    width: fit-content;

    padding: 10px 12px 0px;
    margin-left: -10px;
    margin-top: -2px;

    margin-bottom: 10px;

    line-height: 66px;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: bold;
      font-size: 44px;
      margin-bottom: 0;
      line-height: 51px;

      padding: 2px 11px 0px;
      letter-spacing: 0.025em;
    }

    @media (max-width: 470px) {
      font-size: 30px;
    }
  }
}

.banners__texts {
  width: 53%;
  min-width: 590px;

  @media (max-width: $mobiles-max) {
    min-width: auto;
    width: 100%;
  }
}

.banners__image {
  // width: 48%;
  position: absolute;
  right: -28.5%;
  top: -75px;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.banners__additional {
  background: rgba(255, 255, 255, 0.62);
  border-radius: 15px;
  min-height: 114px;
  margin-top: 32px;
  position: relative;

  padding: 15px 13px 0 8px;

  width: 92%;
  min-width: 300px;

  &:before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 113px;
    height: 113px;
    background-image: url('../img/bonus.png');
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media (max-width: $mobiles-max) {
    width: 100%;
    margin-top: 37px;
    min-height: 137px;

    padding: 23px 13px 0 23px;

    &:before {
      position: absolute;
      content: '';
      left: -5px;
      top: 0;
      width: 134px;
      height: 137px;
      background-image: url('../img/bonus-mob.png');

      @media (max-width: 470px) {
        width: 105px;
        height: 105px;
      }
    }
  }

  @media (max-width: 470px) {
    padding: 6px 13px 0 23px;
    margin-top: 10px;
    min-height: 105px;
    min-width: 287px;
  }

  &:after {
    position: absolute;
    content: '';
    right: 0;
    top: 0;
    width: 69px;
    height: 63px;
    background-image: url('../img/bonus.svg');
  }
}

.banners__quantity {
  display: flex;
  align-items: center;
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 68px;
    line-height: 78px;

    color: #161616;
    margin: 0 4px 0 0;

    @media (max-width: 470px) {
      font-size: 45px;
    }
  }

  span {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;

    color: #161616;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 18px;

      margin-top: 5px;

      color: #161616;
    }

    @media (max-width: 470px) {
      font-size: 12px;
    }
  }
}

.banners__additional-texts {
  padding-left: 120px;

  @media (max-width: 470px) {
    padding-left: 85px;
  }
}

.banners__after {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  margin: 0 0 -6px 0;

  color: $color-default-black;

  @media (max-width: 470px) {
    font-size: 12px;
  }
}

.banners__offer {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  padding-left: 15px;
  text-align: left;
  margin-top: 48px;
  margin-bottom: 11px;
  color: $color-default-white;

  @media (max-width: $mobiles-max) {
    padding-left: 0;
    margin-top: 22px;
    margin-bottom: 15px;
  }
}

.banners__date {
  display: flex;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;

  @media (max-width: $mobiles-max) {
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 115%;
    color: #000000;
  }

  svg {
    margin-right: 12px;
  }
}

.banners__item {
  margin-bottom: 18px;

  display: flex;
  align-items: flex-end;

  @media (max-width: $mobiles-max) {
    align-items: flex-end;
    margin-bottom: 7px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    margin: 0;
    color: $color-default-black;

    @media (max-width: $mobiles-max) {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 22px;

      align-items: baseline;
    }
  }

  svg {
    margin-right: 10px;
    flex-shrink: 0;

    @media (max-width: $mobiles-max) {
      margin-right: 4px;
    }
  }
}

.banners__list {
  @media (max-width: $mobiles-max) {
    width: 320px;
  }

  @media (max-width: 470px) {
    width: 100%;
  }
}

.banners__speaker {
  position: absolute;
  content: '';
  bottom: 0;
  right: 0;

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  text-align: right;
  width: 198px;

  bottom: 150px;
  right: 100px;

  color: #ffffff;

  @media (max-width: 999px) {
    position: relative;
    bottom: auto;
    right: 0;
    width: 100%;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;

    text-align: right;

    color: #ffffff;

    margin: 22px 0 0;
  }

  @media (max-width: 470px) {
    margin: 10px 0 0;
  }
}
