.header {
  width: 100%;
  z-index: 50;
  transform: translateY(0);
  position: absolute;
  transition: all 0.1s;
}

.header__logo {
  width: 182px;
  height: 55px;

  @media (max-width: $mobiles-max) {
    width: 162px;
    height: 49px;
  }
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 0;
}
.header__logo-link {
  display: block;
  height: 100%;
  width: 100%;
}

.header__phone {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  text-align: right;

  @media (max-width: $tablets-max) {
    margin-bottom: 0;
  }

  @media (max-width: $mobiles-max) {
    display: none;
  }
}
