/* stylelint-disable */
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none !important;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

body {
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  margin: 0 auto;
  height: 100%;
  color: #2d2d2d;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

a,
button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
a:focus,
button:focus {
  outline: none !important;
}

h1,
h2,
h3,
h4 {
  font-family: "Bebas Neue", sans-serif;
  color: #000000;
}

h1 {
  font-family: "Bebas Neue", sans-serif;
  font-size: 68px;
  font-style: normal;
  font-weight: 700;
  line-height: 78px;
  letter-spacing: 0.025em;
  margin: 0;
}
@media (max-width: 1023px) {
  h1 {
    font-size: 44px;
    font-style: normal;
    font-weight: 700;
    line-height: 51px;
    letter-spacing: 0.025em;
  }
}

h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 53px;
  margin-top: 0;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  h2 {
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 120%;
  }
}

h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;
  color: #2d2d2d;
}
@media (max-width: 1023px) {
  h3 {
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 120%;
  }
}

h5 {
  font-weight: 800;
  font-size: 20px;
  line-height: 130%;
  margin: 0;
}

h6 {
  font-size: 18px;
  line-height: 22px;
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0 0 20px 0;
}

.hidden {
  display: none i !important;
}

.show {
  display: block i !important;
}

[placeholder] {
  color: #f6f6f6;
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-webkit-input-placeholder {
  color: #f6f6f6;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-moz-placeholder {
  color: #f6f6f6;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

::-moz-placeholder {
  color: #f6f6f6;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-ms-input-placeholder {
  color: #f6f6f6;
  opacity: 1;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus[placeholder] {
  opacity: 0;
  outline: none;
  transition: opacity 0.3s ease;
}

:focus::-webkit-input-placeholder {
  opacity: 0;
  outline: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus::-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-ms-input-placeholder {
  opacity: 0;
  outline: none;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

a {
  color: #2d2d2d;
  text-decoration: none;
  transition: all 0.3s;
}

input,
textarea {
  color: #f4f4f4;
  opacity: 1 !important;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.container {
  width: 100%;
  max-width: 1180px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}
.container-wide {
  max-width: 100%;
}
@media (max-width: 1023px) {
  .container {
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .container {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (max-width: 450px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.v-select,
.vs__dropdown-toggle,
.v-select input {
  cursor: pointer !important;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.error {
  border: 2px solid #dd3232 !important;
}

main {
  flex-grow: 1;
}

.grecaptcha-badge {
  display: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.scroll-off {
  overflow: hidden;
}

@keyframes move-gradient {
  from {
    background-position-x: left;
  }
  to {
    background-position-x: right;
  }
}
.slide-left-enter-active {
  transition: all 0.6s;
}

.slide-left-leave-active {
  transition: all 0.6s;
}

.slide-left-enter,
.slide-left-leave-to {
  transform: translateX(100%);
}

.fade-enter-active,
.fade-leave-active {
  transition-property: opacity;
  transition-duration: 0.3s;
}

.fade-enter-active {
  transition-delay: 0.3s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.header {
  width: 100%;
  z-index: 50;
  transform: translateY(0);
  position: absolute;
  transition: all 0.1s;
}

.header__logo {
  width: 182px;
  height: 55px;
}
@media (max-width: 767px) {
  .header__logo {
    width: 162px;
    height: 49px;
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 0;
}

.header__logo-link {
  display: block;
  height: 100%;
  width: 100%;
}

.header__phone {
  font-family: "Ubuntu", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  text-align: right;
}
@media (max-width: 1023px) {
  .header__phone {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .header__phone {
    display: none;
  }
}

.form {
  background-image: url("../img/bg-footer.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 523px;
  padding: 132px 0;
}
@media (max-width: 1023px) {
  .form {
    padding: 50px 0;
    min-height: 450px;
    background-position: 25.5% 28px;
  }
}
.form__agree {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  white-space: nowrap;
  color: #545454;
}
@media (max-width: 767px) {
  .form__agree {
    white-space: pre-wrap;
  }
}

.form__body {
  display: flex;
}
@media (max-width: 1023px) {
  .form__body {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .form__body {
    padding: 0 5px;
  }
}

.form__send {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 20px;
}
@media (max-width: 767px) {
  .form__send {
    width: 100%;
    margin-top: 14px;
  }
}

.form__texts {
  width: 40%;
  margin-right: 3.5%;
  padding-top: 38px;
}
@media (max-width: 1023px) {
  .form__texts {
    width: 100%;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .form__texts {
    padding-top: 55px;
  }
}

.form__texts-title {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 46px;
  background-color: #fde509;
  padding: 5px 10px 0;
  color: #000000;
  display: inline;
}
@media (max-width: 767px) {
  .form__texts-title {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 115%;
    padding: 5px 5px 0;
  }
}

.form__texts-subtitle {
  font-family: "Bebas Neue", sans-serif;
  background-color: #ebdfff;
  display: block;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 37px;
  margin-bottom: 24px;
  padding-right: 30px;
  color: #000000;
}
@media (max-width: 767px) {
  .form__texts-subtitle {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 115%;
    padding: 5px 5px;
    margin-bottom: 10px;
  }
}

.form__texts-detail {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .form__texts-detail {
    margin-bottom: 16px;
  }
}
.form__texts-detail p {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  margin: 0;
  letter-spacing: 0.4px;
  color: #000000;
}
@media (max-width: 767px) {
  .form__texts-detail p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
  }
}
.form__texts-detail svg {
  flex-shrink: 0;
  margin-right: 10px;
}

.form__texts-marathon {
  margin-top: 63px;
}
@media (max-width: 767px) {
  .form__texts-marathon {
    margin-top: 30px;
  }
}

.form__texts-marathon-title {
  font-family: "Bebas Neue", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  margin-bottom: 8px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #545454;
}

.form__texts-timer {
  display: flex;
  height: 103px;
}
@media (max-width: 1023px) {
  .form__texts-timer {
    justify-content: center;
  }
}
.form__texts-timer p {
  font-style: normal;
  font-weight: bold;
  font-size: 56px;
  line-height: 64px;
  text-align: center;
  letter-spacing: 0.455em;
  text-transform: uppercase;
  background-image: url("../img/group.svg");
  background-repeat: no-repeat;
  color: #ffffff;
  height: 103px;
  width: 111px;
  margin: 0;
  padding-left: 10px;
}
@media (max-width: 767px) {
  .form__texts-timer p {
    background-size: contain;
    width: 106px;
  }
}
@media (max-width: 470px) {
  .form__texts-timer p {
    width: 78px;
    font-size: 40px;
    line-height: 54px;
    padding-left: 8px;
  }
}
.form__texts-timer span {
  font-style: normal;
  font-weight: bold;
  font-size: 56px;
  line-height: 64px;
  text-align: center;
  letter-spacing: 0.455em;
  text-transform: uppercase;
  color: #701df7;
  width: 20px;
  transform: translateX(-2px);
}
.form__texts-timer b {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  transform: translateY(-4px);
  color: #2b2b2b;
}

.form__time {
  display: flex;
  margin-right: 13px;
  flex-direction: column;
}
@media (max-width: 767px) {
  .form__time {
    margin-right: 11px;
  }
  .form__time:last-of-type {
    margin-right: 0;
  }
}

.form__request {
  background: #fe810f;
  border-radius: 25px;
  padding: 127px 158px 100px;
  width: 58.5%;
  min-height: 523px;
  transition: 0.3s all;
}
@media (max-width: 1299px) {
  .form__request {
    padding: 127px 100px;
  }
}
@media (max-width: 1023px) {
  .form__request {
    width: 100%;
    margin-right: 0;
    padding: 45px 45px;
    margin-top: 27px;
    min-height: 348px;
  }
}
@media (max-width: 470px) {
  .form__request {
    padding: 45px 15px;
  }
}

