}
.form__input:valid, .form__input:invalid {
  padding-top: 15px;
}
.form__input--radio {
  width: 0 !important;
  height: 0;
  position: absolute;
  opacity: 0 !important;
  z-index: -1;
}

.form__radio {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 50%;
  vertical-align: text-top;
  margin-right: 10px;
}

.form__radio::before {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.3s all;
}

.form__input--radio:checked + .form__radio::before {
  transform: translate(-50%, -50%) scale(1);
}

