  cursor: pointer;
}
.form__checkbox-input + label::before {
  content: "";
  position: absolute;
  background: #f7f8fa;
  border: 1px solid #dfe6ed;
  border-radius: 2px;
  width: 24px;
  height: 24px;
  left: -33px;
  top: -6px;
}
